import "core-js/modules/es6.array.iterator";
import "core-js/modules/es6.promise";
import "core-js/modules/es7.promise.finally";
import axios from '@/libs/api.request';
/**
 * 添加
 */

var obj = new Object();

obj.addCommon = function (data, fnOk, fnError) {
  return axios.request({
    url: 'commonPzjl/addTemplate',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};
/**
 * 查询
 */


obj.getCommonList = function (data, fnOk, fnError) {
  return axios.request({
    url: "commonPzjl/list?pageIndex=".concat(data.pageIndex, "&pageSize=").concat(data.pageSize),
    method: 'post'
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

